import React from 'react';
import * as Ons from 'react-onsenui';


const renderToolbar = (title, additionalElement) => () =>
    <Ons.Toolbar id="home-toolbar">
        <div className="left">
            <Ons.ToolbarButton>
                <Ons.Icon icon="md-menu"/>
            </Ons.ToolbarButton>
        </div>
        <div className="center">{title}</div>
        {additionalElement}
    </Ons.Toolbar>;


export default renderToolbar;