import {tokenProvider} from "../features/auth/commonAuth";

const buildAuthHeadersBuilder = tokenProvider => () => {
    return new Headers({
        Authorization: `Bearer ${tokenProvider()}`
    });

};

const buildBaseUrlBuilder = spreadsheetId => {
    const baseUrl = `https://sheets.googleapis.com/v4/spreadsheets/${spreadsheetId}`;

    return {
        forSave: range => `${baseUrl}/values/${range}:append?valueInputOption=USER_ENTERED&includeValuesInResponse=true`,
        forUpdate: range => `${baseUrl}/values/${range}?valueInputOption=USER_ENTERED&includeValuesInResponse=true`,
        forFetch: range => `${baseUrl}/values/${range}`,
        forClear: range => `${baseUrl}/values/${range}:clear`,
        forBatchClear: () => `${baseUrl}/values:batchClear`,
        forBatchUpdate: () => `${baseUrl}/values:batchUpdate`,
    }
};

const _fetch = async (url, init) => {
    const r = await fetch(url, init);
    if (!r.ok) {
        const body = await r.text();
        throw new Error(body);
    }
    return r;
};



export const copySpreadsheet = tokenProvider => async (spreadsheetId, name) => {
    const url = `https://www.googleapis.com/drive/v3/files/${spreadsheetId}/copy`;

    const headers = buildAuthHeadersBuilder(tokenProvider)();
    headers.set('Content-Type', 'application/json');
    const init = {
        method: 'POST',
        headers: headers,
        body: JSON.stringify({name})
    };

    const r = await _fetch(url, init);
    const json = await r.json();
    return json.id;

};



export const buildSpreadsheetApi = ({spreadsheetId, tokenProvider}) => {
    const baseUrlBuilder = buildBaseUrlBuilder(spreadsheetId);
    const authHeadersBuilder = buildAuthHeadersBuilder(tokenProvider);

    const appendOrUpdate = (method, urlProvider, valueExtractor) => async (range, values) => {
        const valueRange = {
            range: range,
            majorDimension: 'ROWS',
            values: values
        };

        const headers = authHeadersBuilder();
        headers.set('Content-Type', 'application/json');

        const init = {
            method: method,
            headers: headers,
            body: JSON.stringify(valueRange)
        };

        const r = await _fetch(
            urlProvider(range), init
        );

        const json = await r.json();
        return valueExtractor(json);
    };

    const fetchAll = async range => {
        const r = await _fetch(
            baseUrlBuilder.forFetch(range),
            {
                method: 'GET',
                headers: authHeadersBuilder()
            }
        );
        const json = await r.json();
        return json.values ? json.values : [];
    };

    const clear = async range => {
        const init = {
            method: 'POST',
            headers: authHeadersBuilder(),
        };

        return _fetch(
            baseUrlBuilder.forClear(range),
            init
        );
    };

    const batchClear = async ranges => {
        const headers = authHeadersBuilder();
        headers.set('Content-Type', 'application/json');

        const init = {
            method: 'POST',
            headers: headers,
            body: JSON.stringify({ranges})
        };

        return _fetch(
            baseUrlBuilder.forBatchClear(),
            init
        );
    };

    const batchUpdate = async (ranges) => {
        const headers = authHeadersBuilder();
        headers.set('Content-Type', 'application/json');

        const init = {
            method: 'POST',
            headers: headers,
            body: JSON.stringify({
                valueInputOption: 'USER_ENTERED',
                data:ranges
            })
        };

        return _fetch(
            baseUrlBuilder.forBatchUpdate(),
            init
        );
    };

    const deleteSpreadsheet = async spreadsheetId => {
        const url = `https://www.googleapis.com/drive/v3/files/${spreadsheetId}`
        const init = {
            method: 'DELETE',
            headers: authHeadersBuilder()
        };
        return _fetch(url, init);
    };

    const deleteAll = async range =>
        _fetch(
            baseUrlBuilder.forClear(range),
            {
                method: 'POST',
                headers: authHeadersBuilder()
            }
        );

    return {
        save: appendOrUpdate(
            'POST',
            baseUrlBuilder.forSave,
            json => json.updates.updatedData.values),
        update: appendOrUpdate(
            'PUT',
            baseUrlBuilder.forUpdate,
            json => json.updatedData.values)
        , fetchAll, clear, batchClear, batchUpdate, deleteSpreadsheet, deleteAll
    };
};


