import React from 'react';
import * as Ons from 'react-onsenui';
import {useDispatch, useSelector} from 'react-redux';
import renderToolbar from '../toolbar/Toolbar';
import {FacturesStatus, fetchAllFactures} from "./facturesSlice";

const Factures = (props) => {

    const dispatch = useDispatch();
    const state = useSelector(state => state.factures);
    const loading = state.status === FacturesStatus.loading;
    const active = props.activeIndex === 0;

    if(state.status === FacturesStatus.refresh && active)
        dispatch(fetchAllFactures());

    const renderFacture = facture =>
        <Ons.ListItem key={facture.id}>
            {facture.name}
            <div className={'right'}>
                <a href={`https://docs.google.com/spreadsheets/d/${facture.spreadsheet}`} target={'_blank'}>
                    <Ons.Button
                        onClick={e => e.stopPropagation()}
                        disabled={loading}
                    >
                        <Ons.Icon icon="fa-print"/>
                    </Ons.Button>
                </a>
            </div>
        </Ons.ListItem>;

    return <Ons.Page title="Home"
                     active={props.activeIndex === 0}
                     renderToolbar={renderToolbar('Factures')}
                     tabbar={props.tabbar}>
        <Ons.ProgressBar indeterminate={loading} />

        <Ons.List
            dataSource={state.factures}
            renderRow={renderFacture}
        />
    </Ons.Page>

};


const factureTab = (activeIndex, tabbar) => {
    return {
        content: <Factures activeIndex={activeIndex} tabbar={tabbar}/>,
        tab: <Ons.Tab label="Factures" icon="md-receipt"/>
    }
};



export default factureTab;