import {buildApi} from "./commonApi2";
import {buildSpreadsheetApi, copySpreadsheet} from "./sheetsApi";
import {tokenProvider} from "../features/auth/commonAuth";

const allRange = 'devis!A2:L100';
export const devisExploder = ({
                           id,
                           name,
                           to,
                           address1,
                           address2,
                           date,
                           zip,
                           city,
                           unitPrice,
                           quantity,
                           menuId,
                           menu,
                           spreadsheet
                       }) => {
    return [
        id,
        name,
        to,
        address1,
        address2,
        date,
        zip,
        city,
        unitPrice,
        quantity,
        menuId ? menuId : menu.id,
        spreadsheet
    ]
};


export const devisHydrator = ([
                            id,
                            name,
                            to,
                            address1,
                            address2,
                            date,
                            zip,
                            city,
                            unitPrice,
                            quantity,
                            menuId,
                            spreadsheet
                        ]) => {
    return {
        id,
        name,
        to,
        address1,
        address2,
        date,
        zip,
        city,
        unitPrice,
        quantity,
        menuId,
        spreadsheet
    }
};
const baseApi = buildApi({
    firstRowRange: 'devis!A2:L2',
    allRowsRange: allRange,
    entityHydrator: devisHydrator,
    entityExploder: devisExploder
});

const generateReadableDate = date =>
    `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;

export const buildRanges = devis =>
    [
        {
            range: 'to',
            majorDimension: 'ROWS',
            values: [[devis.to]]
        },
        {
            range: 'address1',
            majorDimension: 'ROWS',
            values: [[devis.address1]]
        },
        {
            range: 'address2',
            majorDimension: 'ROWS',
            values: [[devis.address2]]
        },
        {
            range: 'city',
            majorDimension: 'ROWS',
            values: [[devis.city]]
        },
        {
            range: 'zip',
            majorDimension: 'ROWS',
            values: [[devis.zip]]
        },
        {
            range: 'menu',
            majorDimension: 'ROWS',
            values: [[devis.menu.name]]
        },
        {
            range: 'name',
            majorDimension: 'ROWS',
            values: [[devis.name]]
        },
        {
            range: 'quantity',
            majorDimension: 'ROWS',
            values: [[devis.quantity]]
        },
        {
            range: 'unitPrice',
            majorDimension: 'ROWS',
            values: [[devis.unitPrice]]
        },
        {
            range: 'creationDate',
            majorDimension: 'ROWS',
            values: [[generateReadableDate(new Date())]]
        },
        {
            range: 'eventDate',
            majorDimension: 'ROWS',
            values: [[generateReadableDate(new Date(devis.date))]]
        },
        {
            range: 'plats',
            majorDimension: 'COLUMNS',
            values: [devis.menu.plats.map(({name}) => name)]
        }
    ];

export const populatePrintable = async (spreadsheetId, ranges) => {

    const api = buildSpreadsheetApi({
        spreadsheetId,
        tokenProvider
    });


    await api.batchClear(ranges.map(({range})=>range));
    await api.batchUpdate(ranges);

};

export const buildCopyTemplate = (baseName, baseSpreadsheetId) => async devis => {
    if (devis.spreadsheet !== '')
        return Promise.resolve(devis.spreadsheet);


    return await copySpreadsheet(tokenProvider)(
        baseSpreadsheetId,
        `${baseName}_${devis.name}_${devis.id}`
    );
};

const createPrintableDevis =
    buildCopyTemplate('Devis', '1eH-4W9Q0DxhI763WGxn5BFgdzTyHinLNzQ1e19jMZD8');



const devisApi = {
    ...baseApi,
    upsert: async devis => {
        const id = await baseApi.upsert(devis);
        const persistedDevis = {
            ...devis,
            id
        };
        const spreadsheedId = await createPrintableDevis(persistedDevis);
        const devisWithSpreadsheet = {
            ...persistedDevis,
            spreadsheet: spreadsheedId
        };
        await populatePrintable(devisWithSpreadsheet.spreadsheet, buildRanges(devisWithSpreadsheet));

        return baseApi.upsert(devisWithSpreadsheet);
    },
    deleteDevis: async devis => {
        await baseApi.deleteSpreadsheet(devis.spreadsheet);
        return baseApi.deleteEntity(devis);
    }
};

export default {
    ...devisApi,
    createPrintableDevis
};


