import {createSlice} from '@reduxjs/toolkit';

const initialState = {
    page: ''
};

export const navSlice = createSlice({
    name: 'nav',
    initialState,
    reducers:{
        newPlat: (state) => {
            state.page = 'newPlat'
        }
    }
});

export const {newPlat} = navSlice.actions;

export const selectPage = state => {
    return state.nav.page;
};

export default navSlice.reducer;