import * as Ons from 'react-onsenui';
import React from 'react';
import {useDispatch, useSelector} from 'react-redux';
import renderToolbar from "../toolbar/Toolbar";
import {setNewMenuName, addPlat, removePlat, resetMenuForm} from "./menusSlice";
import {fetchPlats,searchPlat, initPlats,saveNewMenu} from "./menusSlice";

const NewMenu = props => {

    const dispatch = useDispatch();

    const ok = () => dispatch(saveNewMenu());
    const platsStatus = useSelector(state => state.menus.status);
    const state = useSelector(state => state);
    // if(platsStatus=== 'refresh') dispatch(fetchPlats());

    const plats = useSelector(({menus: {plats}}) => plats);
    const searchText = useSelector(state => state.menus.searchText);
    const foundPlats = useSelector(state => state.menus.foundPlats);
    const menu = useSelector(state => state.menus.menu);
    const showProgress = useSelector(state => state.menus.showProgress);


    const renderSelectedPlat = plat =>
        <Ons.ListItem key={plat.id}>
            {plat.name}
            <div className="right">
                <Ons.Button onClick={() => dispatch(removePlat(plat))}>
                    <Ons.Icon icon="fa-trash-alt"/>
                </Ons.Button>
            </div>
        </Ons.ListItem>;

    const renderSelectedPlats = () =>
        <Ons.List
            dataSource={menu.plats}
            renderRow={renderSelectedPlat}
        />;

    const renderFoundPlat = plat =>
        <Ons.ListItem key={plat.id}>
            {plat.name}
            <div className="right">
                <Ons.Button onClick={() => dispatch(addPlat(plat))}>
                    <Ons.Icon icon="md-plus"/>
                </Ons.Button>
            </div>
        </Ons.ListItem>;

    const renderFoundPlats = () =>
        <Ons.List
            dataSource={foundPlats}
            renderRow={renderFoundPlat}
        />;

    const toolbarButtons =
        <div className="right">
            <Ons.ToolbarButton
                disabled={showProgress}
                onClick={ok}>OK</Ons.ToolbarButton>
            <Ons.ToolbarButton
                disabled={showProgress}
                onClick={() => dispatch(resetMenuForm())}>
                <Ons.Icon icon="fa-window-close"/>
            </Ons.ToolbarButton>
        </div>;

    return <Ons.Page
        title="Nouveau menu"
        renderToolbar={renderToolbar(
            menu.id === ''? 'Nouveau menu': 'diter menu',
            toolbarButtons)}>
        <Ons.ProgressBar indeterminate={showProgress} />

        <div className="form">
            <Ons.Input
                value={menu.name}
                onChange={event => dispatch(setNewMenuName(event.target.value)) }
                placeholder='Nom' />

        </div>
            <br/>
            <div>Plats</div>
        {renderSelectedPlats()}
            <div>Ajouter des Plats</div>
        <div  style={{textAlign: 'center', marginTop: '10px'}}>
        <Ons.SearchInput
            placeholder="Rechercher"
            value={searchText}
            onChange={e => dispatch(searchPlat(e.target.value))}
            />

        </div>
        {renderFoundPlats()}
    </Ons.Page>
};

export default NewMenu;