import * as Ons from 'react-onsenui';
import {useSelector, useDispatch} from 'react-redux'
import {AuthState, loadClient, loadScript, setUser} from "./googleAuthSlice";


const GoogleAuthRdx = props => {

    const authState = useSelector(state => state.auth.state);
    const user = useSelector(state => state.auth.user);

    const dispatch = useDispatch();

    const serializeGoogleUser = user => {
        return user.isSignedIn() ?
            {
                name: user.getBasicProfile().getName(),
                email: user.getBasicProfile().getEmail(),
                auth: user.getAuthResponse(),
                isSignedIn: user.isSignedIn()
            } :
            {
                isSignedIn: user.isSignedIn()
            };
    };

    const setGUser = () => {
        const user = window.GoogleAuth.currentUser.get();
        dispatch(
            setUser(
                serializeGoogleUser(user)
            )
        );
    };


    window.GoogleAuth.isSignedIn.listen(setGUser);

    switch(authState){
        case AuthState.userNotLoaded:
            setGUser();
            return <p>Loading profile</p>
        case AuthState.userLoaded:
            return user.isSignedIn ?
                <div>{props.children}</div> :
                <Ons.Button
                    onClick={()=> window.GoogleAuth.signIn()}
                >Authentification</Ons.Button>;


    }

};

export default GoogleAuthRdx;