import * as Ons from 'react-onsenui';
import {useDispatch, useSelector} from 'react-redux';
import factureTab from './features/factures/Factures';
import devisTab from './features/devis/Devis';
import menusTab from './features/menus/Menus';
import platsTab from "./features/plats/Plats";
import {setActiveTab} from "./appSlice";

import './App.css'
import {setReload} from "./features/devis/devisSlice";

function App(props) {


    const tabIndex = useSelector(state => state.app.tabIndex);
    const dispatch = useDispatch();


    const page = (route, navigator) => {
        return <Ons.Page>
            <Ons.Tabbar
                onPostChange={({index}) => dispatch(setActiveTab(index))}
                onReactive={() => console.log('postChange')}
                position='bottom'
                index={tabIndex}
                renderTabs={(activeIndex, tabbar) => [
                    factureTab(activeIndex, tabbar),
                    devisTab(activeIndex, tabbar),
                    menusTab(activeIndex, tabbar),
                    platsTab(activeIndex, tabbar, route, navigator)
                ]
                }
            />
        </Ons.Page>;
    };


    return <Ons.Navigator
        renderPage={page}
        initialRoute={{
            title: 'Factures'
        }}/>;

}

export default App
