import {createAsyncThunk, createSlice} from '@reduxjs/toolkit';


const initialState = {
    tabIndex: 0

};

const appSlice = createSlice({
    name: 'app',
    initialState,
    reducers: {
        activateDevis: state => {
            state.tabIndex = 1
        },
        activateFactures: state => {
            state.tabIndex = 0;
        },
        setActiveTab: (state, {payload}) => {
            state.tabIndex = payload;
        }
    }
});

export const {activateDevis, activateFactures, setActiveTab} = appSlice.actions;

export default appSlice.reducer;