import {createAsyncThunk, createSlice} from '@reduxjs/toolkit';
import platsApi from "../../api/platApi";


export const PlatsStatus = {
    loading: 'loading',
    loaded: 'loaded',
    failed: 'failed',
    refresh: 'refresh'
};

const initialState = {
    plats: [],
    status: PlatsStatus.refresh,
    platName:'',
    platId: ''
};

const fetchPlats = createAsyncThunk(
    'plats/fetchAll',
    platsApi.fetchAll
);

export const newPlat = createAsyncThunk(
    'plats/save',
    platsApi.upsert
);

export const deletePlat = createAsyncThunk(
    'plats/delete',
    platsApi.deleteEntity
);

const platsSlice = createSlice({
    name: 'plats',
    initialState,
    reducers: {
        setPlatName:(state, {payload})=>{
            state.platName = payload;
        },
        setPlat:(state, {payload:{id, name}})=>{
            state.platName = name;
            state.platId = id;
        }

    },
    extraReducers(builder){
        builder
            .addCase(
                fetchPlats.pending,
                (state, action) => {
                    state.status = PlatsStatus.loading
                }
            )
            .addCase(
                fetchPlats.rejected,
                (state, action) => {
                    state.status = PlatsStatus.failed
                }
            )
            .addCase(
                fetchPlats.fulfilled,
                (state, action) => {
                    state.plats = action.payload;
                    state.status = PlatsStatus.loaded;
                }
            ).addCase(
                newPlat.fulfilled,
                state => {
                    state.status = PlatsStatus.refresh;
                    state.platName = '';
                    state.platId = '';
                }
            ).addCase(
                deletePlat.pending,
                state => {
                    state.status = PlatsStatus.loading
                }
            ).addCase(
                deletePlat.fulfilled,
                state => {
                    state.status = PlatsStatus.refresh
                }
            );
    }
});

export {fetchPlats};

export const {setPlatName, setPlat} = platsSlice.actions;

export const selectPlats = state => state.plats.plats;

export default platsSlice.reducer;