import {createAsyncThunk, createSlice} from '@reduxjs/toolkit';
import factureApi from '../../api/factureApi';

export const FacturesStatus = {
    loading: 'loading',
    loaded: 'loaded',
    refresh: 'refresh'
};

const initialState = {
    factures: [],
    status: FacturesStatus.refresh
};

export const fetchAllFactures = createAsyncThunk(
    'factures.fetchAll',
    factureApi.fetchAll
);

const errorCb = (state, payload) => {
    state.status = FacturesStatus.loaded;
    state.error = JSON.stringify(payload);
    state.error = state.error === '' ? 'No error message' : state.error;
    console.error('Failed: ', payload);
};


const factureSlice = createSlice({
    name: 'factures',
    initialState,
    reducers: {
        refresh: state => {
            state.status = FacturesStatus.refresh;
        }
    },
    extraReducers(builder) {
        builder.addCase(
            fetchAllFactures.pending,
            state => {
                state.status = FacturesStatus.loading;
            }
        ).addCase(
            fetchAllFactures.rejected,
            errorCb
        ).addCase(
            fetchAllFactures.fulfilled,
            (state, {payload: factures}) => {
                state.factures = factures;
                state.status = FacturesStatus.loaded
            }
        )
    }
});

export const {refresh} = factureSlice.actions;

export default factureSlice.reducer;