import {createSlice} from '@reduxjs/toolkit';

export const AuthState = {
    userNotLoaded: 'userNotLoaded',
    userLoaded: 'userLoaded'
};


const initialState = {
    state: AuthState.userNotLoaded,
    user: {
        name: '',
        email: '',
        auth:{},
        isSignedIn: false
    }
};


const gauthSlice = createSlice({
    name: 'gauth',
    initialState,
    reducers: {
        setUser: (state, {payload:user})=>{
            state.state = AuthState.userLoaded;
            state.user= user;
        }
    }

});

export const {setUser} = gauthSlice.actions;

export default gauthSlice.reducer;

