import {configureStore} from '@reduxjs/toolkit';
import navReducer from '../features/nav/navSlice';
import platsReducer from '../features/plats/platsSlice';
import menusReducer from '../features/menus/menusSlice';
import appReducer from '../appSlice';
import devisReducer from '../features/devis/devisSlice';
import gauthReducer from '../features/auth/googleAuthSlice';
import factureReducer from '../features/factures/facturesSlice';

export const store = configureStore({
    reducer: {
        app: appReducer,
        nav: navReducer,
        plats: platsReducer,
        menus: menusReducer,
        devis: devisReducer,
        auth: gauthReducer,
        factures: factureReducer
    },
});
