import platsApi from "./platApi";
import {buildApi} from "./commonApi2";


const allRange = 'menus!A2:B100';

const allRangeMenusPlats = 'menus__plats!A2:B100';
const menuPlatExploder = ({menuId, platId}) => [menuId, platId];
const menuPlatIdExtractor = ({menuId, platId}) => `${menuId}__${platId}`;
const entityExploder = ({id, name}) => [id, name];

const menuToMenuPlats = menu =>
    menu.plats.map(({id}) => {
            return {
                menuId: menu.id,
                platId: id
            };
        }
    );

const menusPlatsBaseApi = buildApi({
    firstRowRange: 'menus__plats!A2:B2',
    allRowsRange: 'menus__plats!A2:B100',
    entityExploder: menuPlatExploder,
    entityHydrator: ([menuId, platId]) => ({menuId, platId}),
    idExtractor: menuPlatIdExtractor
});

const menusPlatsApi = {
    ...menusPlatsBaseApi
};

const baseApi = buildApi({
    firstRowRange: 'menus!A2:B2',
    allRowsRange: allRange,
    entityHydrator: ([id, name]) => ({id, name}),
    entityExploder: entityExploder
});
const menusApi = {
    ...baseApi,
    findByMenu: async ({id}) => {
        const allMenus = await baseApi.fetchAll();
        const menu = allMenus.find(({id: _id}) => id === _id);

        const menusPlats = await menusPlatsApi.fetchAll();
        const mpFiltered = menusPlats.filter(({menuId}) => menuId === id);
        const platIds = mpFiltered.map(({platId}) => platId);

        const plats = await platsApi.fetchAll()
            .then(ps =>
                ps.filter(({id: platId}) => platIds.find(i => i === platId))
            );

        return {
            ...menu,
            plats
        }
    },
    upsert: async menu => {
        const _menuId = await baseApi.upsert(menu);
        const savedMenu = {
            ...menu,
            id: _menuId
        };
        const menusPlats = await menusPlatsApi.fetchAll();
        await menusPlatsApi.deleteAll();
        return menusPlatsApi.saveAll([
            ...menusPlats.filter(({menuId}) => menuId !== _menuId),
            ...menuToMenuPlats(savedMenu)
        ]);
    },
    deleteEntity: async menu =>{
        const menusPlats = await menusPlatsApi.fetchAll();
        await menusPlatsApi.deleteAll();
        menusPlatsApi.saveAll(
            menusPlats.filter(({menuId}) => menuId !== menu.id)
        );
        await baseApi.deleteEntity(menu);
    }
};



export default menusApi;


