import {buildSpreadsheetApi} from "./sheetsApi";
import {tokenProvider} from "../features/auth/commonAuth";

const spreadsheetId = '1YFl4juh1YiAFG5PmiJnvzq88mocCtjleaTh7p3pZlTY';

// extremely unsafe but sufficient...
const generateId = () => Date.now();

const sheetApi = buildSpreadsheetApi({
    spreadsheetId,
    tokenProvider
});


const save = (
    range,
    entityExploder) => async entity => {

    const vals = entityExploder(entity);
    const [id, ...withoutId] = vals;
    const toSave = [
        [id === '' ? generateId() : id, ...withoutId]
    ];

    const newVals = await sheetApi.save(range, toSave);
    return newVals[0][0];
};

export const fetchAll = (
    range,
    entityHydrator
) => async () => {

    const values = await sheetApi.fetchAll(range);
    return values.map(entityHydrator);
};

export const saveAll = (range, entityExploder) => async entities => {

    const toSave = entities.map(entityExploder);

    return await sheetApi.update(range, toSave);
};


const update = async (range, allFetcher, entityExploder, idExtractor, entity) => {
    const entities = await allFetcher();
    return saveAll(
        range,
        entityExploder
    )(
        entities.map(e =>
            idExtractor(e) === idExtractor(entity) ?
                entity : e
        )
    );
};

const defaultIdExtractor = ({id}) => id;

export const upsert = (
    range,
    allRange,
    entityExploder,
    allFetcher,
    idExtractor = defaultIdExtractor
) => async entity => {
    const entities = await allFetcher();
    if( ! entities.find((e)=> idExtractor(e)=== idExtractor(entity)) )
        return save(range, entityExploder)(entity);

    await update(
        allRange,
        () => entities,
        entityExploder,
        idExtractor,
        entity
    );
    return idExtractor(entity);
};

export const buildApi = ({
                      firstRowRange,
                      allRowsRange,
                      entityHydrator,
                      entityExploder,
                      idExtractor = defaultIdExtractor,
                  }) => {

    const _fetchAll = fetchAll(allRowsRange, entityHydrator);


    const deleteEntity = async entity => {
        const all = await _fetchAll();
        const toSave = all.filter(e =>
            idExtractor(e) !== idExtractor(entity)
        );
        await sheetApi.deleteAll(allRowsRange);

        return saveAll(
            allRowsRange,
            entityExploder
        )(toSave);
    };

    return {
        fetchAll: _fetchAll,
        upsert: upsert(firstRowRange, allRowsRange, entityExploder, _fetchAll, idExtractor),
        deleteEntity,
        deleteSpreadsheet : sheetApi.deleteSpreadsheet,
        deleteAll: async () => sheetApi.deleteAll(allRowsRange),
        save: save(firstRowRange, entityExploder),
        saveAll: saveAll(allRowsRange, entityExploder)
    }
};
