import {buildCopyTemplate, buildRanges, devisExploder, devisHydrator, populatePrintable} from "./devisApi";
import {buildApi} from "./commonApi2";


const createPrintableFacture =
    buildCopyTemplate('Facture', '1QI8eY0LTArvBisAj7_vtehRLDhFFZkQo-nxeEFjk87c');

const createFacture = async devis => {
    const transientFacture = {
        ...devis,
        spreadsheet: ''
    };
    const factureSpreadsheetId = await createPrintableFacture(transientFacture);

    await populatePrintable(
        factureSpreadsheetId,
        [
            ...buildRanges(devis),
            {
                range: 'factNum',
                majorDimension: 'COLUMNS',
                values: [[devis.id]]
            }
        ]
    );
    return factureSpreadsheetId;
};

const baseApi = buildApi({
    firstRowRange: 'factures!A2:L2',
    allRowsRange: 'factures!A2:L100',
    entityHydrator: devisHydrator,
    entityExploder: devisExploder
});


const factureApi = {
    ...baseApi,
    save: async devis => {
        const transientFacture = {
            ...devis,
            id: ''
        };
        const id = await baseApi.save(transientFacture);
        const persistedFacture = {
            ...devis,
            id
        };
        const spreadsheedId = await createFacture(persistedFacture);
        const factureWithSpreadsheet = {
            ...persistedFacture,
            spreadsheet: spreadsheedId
        };

        return baseApi.upsert(factureWithSpreadsheet);
    },
    deleteDevis: async devis => {
        await baseApi.deleteSpreadsheet(devis.spreadsheet);
        return baseApi.deleteEntity(devis);
    }
};

export default factureApi;
