import {createAsyncThunk, createSlice} from '@reduxjs/toolkit';
import menusApi, {fetchAllMenusShallow, findByMenu} from "../../api/MenuApi";
import platsApi from "../../api/platApi";

const initialState = {
    menus: [],
    status: 'refresh',
    newMenu: false,
    plats:[],
    searchText: '',
    foundPlats: [],
    showProgress: false,
    menu: {
        id:'',
        name: '',
        plats:[]
    }
};                                              

const fetchMenus = createAsyncThunk(
    'menus.fetchAll',
    menusApi.fetchAll
);

const deleteMenu = createAsyncThunk(
    'menus.delete',
    menusApi.deleteEntity
);

export const loadMenu = createAsyncThunk(
    'menu.load',
    menusApi.findByMenu
);

const saveNewMenu = createAsyncThunk(
    'menus.saveNewMenu',
    async (arg, thunkAPI) => {
        const {menus: {menu}} =
            thunkAPI.getState();
        const eventualSavedMenu = menusApi.upsert(menu);
        eventualSavedMenu.then(() => {
            thunkAPI.dispatch(fetchMenus());
            return thunkAPI.dispatch(resetMenuForm());
            }
        );
        return eventualSavedMenu
    }
);

export const fetchPlats = createAsyncThunk(
    'menus/fetchAllPlats',
    platsApi.fetchAll
);



const menusSlice = createSlice({
    name: 'menus',
    initialState,
    reducers: {
        newMenu:state => {
            state.newMenu = true;
            state.status = 'refresh';
        },
        addPlat: (state, {payload}) => {
            if(!state.menu.plats.find(({id}) => id === payload.id)){
                state.menu.plats.push(payload);
                state.foundPlats = state.foundPlats.filter(({id}) => id !== payload.id);
            }
        },
        setNewMenuName: (state, action) =>{
            state.menu.name = action.payload
        },
        removePlat: (state, {payload}) => {
            state.menu.plats = state.menu.plats.filter(({id}) => id !== payload.id);
            state.foundPlats.push(payload);
            state.foundPlats = [];
        },
        searchPlat: (state, action) => {
            state.searchText = action.payload;
            if (state.searchText === '') {
                state.foundPlats = [];
            }else {
                const preFiltered = state.plats.filter(p =>
                    !state.menu.plats.find(plat =>
                        p.id === plat.id
                    )
                );
                state.foundPlats = preFiltered.filter(({name}) =>
                    name.toLowerCase().includes(state.searchText.toLowerCase())
                );
            }
        },
        resetMenuForm: state => {
            state.searchText ='';
            state.menu = initialState.menu;
            state.foundPlats = [];
            state.newMenu = false;
        },
        editMenu: (state, {payload:menu} ) => {
            state.menu = menu;
            state.newMenu = true;
        }
    },
    extraReducers(builder) {
        builder.addCase(
            fetchMenus.pending,
            state => {
                state.showProgress = true;
                state.status = 'loading';
            }
        ).addCase(
            fetchMenus.rejected,
            state => {
                state.status = 'failed';
                state.showProgress = false;
            },
        ).addCase(
            fetchMenus.fulfilled,
            (state, action) => {
                state.menus = action.payload;
                state.showProgress = false;
            }
        ).addCase(
            saveNewMenu.fulfilled,
            state =>{
                state.newMenu = false;
                state.showProgress = false;
            }
        ).addCase(
            saveNewMenu.pending,
            state => {
                state.showProgress = true;
            }
        ).addCase(
            saveNewMenu.rejected,
            state => {
                state.showProgress = false;
            }
        ).addCase(
            deleteMenu.fulfilled,
            state => {
                state.showProgress = false;
                state.status = 'refresh';
            }
        ).addCase(
            deleteMenu.pending,
            state => {
                state.showProgress = true;
            }
        ).addCase(
            deleteMenu.rejected,
            state => {
                state.showProgress = false;
            }
        ).addCase(
            loadMenu.fulfilled,
            (state, {payload:menu}) => {
                state.newMenu = true;
                state.menu = menu;
                state.showProgress = false;
            }
        ).addCase(
            loadMenu.pending,
            state => {
                state.showProgress = true;
            }
        ).addCase(
            loadMenu.rejected,
            state => {
                state.showProgress = false;
            }
        ).addCase(
            fetchPlats.fulfilled,
            (state, {payload:plats}) => {
                state.showProgress = false;
                state.plats = plats;
            }
        ).addCase(
            fetchPlats.pending,
            state => {
                state.status = 'loading';
                state.showProgress = true;
            }
        ).addCase(
            fetchPlats.rejected,
            state => {
                state.status = 'failed';
                state.showProgress = false;
            }
        )
    }
});

export const {newMenu, addPlat, setNewMenuName, removePlat} = menusSlice.actions;
export const {searchPlat, initPlats, resetMenuForm, editMenu} = menusSlice.actions;

export {fetchMenus, saveNewMenu, deleteMenu};

export const selectMenus = state => state.menus.menus;

export default menusSlice.reducer;