import * as Ons from 'react-onsenui';
import React from 'react';
import renderToolbar from "../toolbar/Toolbar";
import {useDispatch, useSelector} from 'react-redux';
import {newPlat, setPlatName} from "./platsSlice";


const Plat = props => {

    const dispatch = useDispatch();
/*
    useEffect(() => {
        dispatch(updateTitle('Nouveau Plat'));
    });
*/

    const name = useSelector(state => state.plats.platName);
    const id = useSelector(state => state.plats.platId);

    const ok = () => {
        dispatch(newPlat({id, name}));
        props.navigator.popPage();
    };

    return <Ons.Page title="Nouveau Plat"
                     renderToolbar={renderToolbar('Nouveau plat')}>
        <div className='form'>
            <Ons.Input
                value={name}
                onChange={(event) => { dispatch(setPlatName(event.target.value))} }
                placeholder='Nom' />
        </div>
        <Ons.Button modifier="large--cta" onClick={ok}>
            OK
        </Ons.Button>

    </Ons.Page>
};

export default Plat;