import * as Ons from 'react-onsenui';
import React from 'react';
import {useDispatch, useSelector} from 'react-redux';
import renderToolbar from "../toolbar/Toolbar";
import {editMenu, fetchMenus, newMenu, selectMenus, deleteMenu, loadMenu, fetchPlats} from "./menusSlice";
import NewMenu from './NewMenu';
import {activateDevis} from "../../appSlice";
import {loadMenuForDevi, newDevi} from "../devis/devisSlice";

const Menus = props => {
    const dispatch = useDispatch();

    const displayNewMenu = useSelector(state => state.menus.newMenu);

    const status = useSelector(state => state.menus.status);
    const menus = useSelector(selectMenus);
    const showProgress = useSelector(state => state.menus.showProgress);
    if (displayNewMenu) return <NewMenu/>;

    const newDevis = menu => {
        dispatch(loadMenuForDevi(menu));
        dispatch(activateDevis());
    };

    const editMenu = menu => {
        dispatch(loadMenu(menu));
        dispatch(fetchPlats());
    };


    if (props.active && status ==='refresh') dispatch(fetchMenus());

    const renderMenu = (menu, idx) =>
        <Ons.ListItem
            modifier="chevron" tappable={!showProgress}
            onClick={() => editMenu(menu)}
            key={menu.id}
        >
            {menu.name}
            <div className="right">
                <Ons.Button
                    onClick={e =>{
                        e.stopPropagation();
                        newDevis(menu);
                    }}
                    disabled={showProgress}
                >
                    <Ons.Icon icon="fa-file-invoice-dollar"/>
                </Ons.Button>
                &nbsp;
                <Ons.Button
                    onClick={(e) => {
                        e.stopPropagation();
                        dispatch(deleteMenu(menu));
                    }}
                    disabled={showProgress}
                >
                    <Ons.Icon icon="fa-trash-alt"/>
                </Ons.Button>
            </div>
        </Ons.ListItem>;

    const renderMenus = () =>
        <Ons.List
            dataSource={menus}
            renderRow={renderMenu}
        />;

    const showNewMenu = () => dispatch(newMenu());


    return <Ons.Page title="Menus"
                     active={props.active}
                     renderToolbar={renderToolbar('Menus')}
                     tabbar={props.tabbar}>
        <Ons.ProgressBar indeterminate={showProgress} />
        {renderMenus()}
        <Ons.Fab position="bottom right" onClick={showNewMenu}>
            <Ons.Icon icon='md-plus'/>
        </Ons.Fab>

    </Ons.Page>

}

const menusTab = (activeIndex, tabbar) => {

    return {
        content: <Menus active={activeIndex === 2} tabbar={tabbar}/>,
        tab: <Ons.Tab label="Menus" icon="fa-elementor"/>
    }
};

export default menusTab;