import * as Ons from 'react-onsenui';
import React from 'react';
import {useDispatch, useSelector} from 'react-redux';
import renderToolbar from "../toolbar/Toolbar";
import {
    closeEdit, createFacture,
    deleteDevis,
    DevisStatus,
    editDevi,
    errorReset,
    fetchAllDevis,
    loadMenuForDevi,
    save,
    setDevi
} from "./devisSlice";
import Error from '../error/Error';

const Devi = ({devi, status,state}) => {

    const dispatch = useDispatch();

    const setStoreDevis = (e, maj) => {

        e.stopPropagation();
        dispatch(setDevi({
                ...devi,
                ...maj
            })                    
        );
    };

    const loading = status === DevisStatus.loading;

    const tbButtons =
        <div className="right">
            <Ons.ToolbarButton
                disabled={loading}
                onClick={() => {
                dispatch(save(devi))
            }}>OK</Ons.ToolbarButton>
            <Ons.ToolbarButton
                disabled={loading}
                onClick={() => dispatch(closeEdit())}>
                <Ons.Icon icon="fa-window-close"/>
            </Ons.ToolbarButton>
        </div>;
    return <Ons.Page
        title="Nouveau Devis"
        renderToolbar={renderToolbar('Nouveau devis', tbButtons)}
    >
        <Ons.ProgressBar indeterminate={loading} />

        <Error error={state.error} resetErr={()=> dispatch(errorReset())}/>


        <div className={'form'}>
            <Ons.Input
                value={devi.name}
                onChange={event =>
                    setStoreDevis(event, {name: event.target.value})
                }
                placeholder='Objet'/>
        </div>
        <div className={'form'}>
            <input
                value={devi.date}
                onChange={event =>
                    setStoreDevis(event, {date: event.target.value})
                }
                type={'date'}/>
        </div>

        <h3>Destinataire: </h3>
        <div className={'form'}>
            <Ons.Input
                value={devi.to}
                onChange={event =>
                    setStoreDevis(event, {to: event.target.value})
                }
                placeholder='Nom'/>
        </div>
        <div className={'form'}>
            <Ons.Input
                value={devi.address1}
                onChange={event =>
                    setStoreDevis(event, {address1: event.target.value})
                }
                placeholder='Adresse 1'/>
        </div>
        <div className={'form'}>
            <Ons.Input
                value={devi.address2}
                onChange={event =>
                    setStoreDevis(event, {address2: event.target.value})
                }
                placeholder='Adresse 2'/>
        </div>
        <div className={'form'}>
            <Ons.Input
                value={devi.zip}
                onChange={event =>
                    setStoreDevis(event, {zip: event.target.value})
                }
                placeholder='Code Postal'/>
            <Ons.Input
                value={devi.city}
                onChange={event =>
                    setStoreDevis(event, {city: event.target.value})
                }
                placeholder='Ville'/>
        </div>
        <section style={{padding: '10px'}}>
            <h3>
                <Ons.Icon icon={'fa-elementor'}
                          style={{color: 'grey'}}/>
                &nbsp;Menu: {devi.menu.name}
            </h3>
            <table>
                <tbody>
                    <tr>
                        <td>Prix unitaire:</td>
                        <td>Quantit:</td>
                    </tr>
                    <tr>
                        <td>
                            <Ons.Input
                                className={'form'}
                                type={'number'}
                                onChange={event =>
                                    setStoreDevis(event, {unitPrice: event.target.value})
                                }

                                value={devi.unitPrice}
                                placeholder={'0.0'}
                            />
                        </td>
                        <td>
                            <Ons.Input
                                className={'form'}
                                type={'number'}
                                value={devi.quantity}
                                onChange={event =>
                                    setStoreDevis(event, {quantity: event.target.value})
                                }
                                placeholder={'1'}
                            />
                        </td>
                    </tr>
                    <tr>
                        <td colSpan={2} className={'devisPrice'}>{devi.unitPrice * devi.quantity} </td>
                    </tr>
                </tbody>
            </table>
        </section>
        <section style={{padding: '10px'}}>
            <h3><Ons.Icon icon={'fa-pizza-slice'}
                          style={{color: 'grey'}}/>&nbsp;
                Plats:</h3>
            <Ons.List
                dataSource={devi.menu.plats}
                renderRow={({name}, idx) =>
                    <Ons.ListItem key={idx}>
                        {name}
                    </Ons.ListItem>
                }
            />

        </section>


    </Ons.Page>;
};


const Devis = props => {

    const dispatch = useDispatch();

    const active = props.activeIndex === 1;

    const status = useSelector(state => state.devis.status);
    const devis = useSelector(state => state.devis.devis);
    const editDevis = useSelector(state => state.devis.editDevis);
    const state = useSelector(state => state.devis);
    if(! editDevis && status==='' && props.activeIndex===1) dispatch(fetchAllDevis());
    const loading = state.status === DevisStatus.loading;

    const runCreateFacture = devis => e => {
        e.stopPropagation();
        dispatch(createFacture(devis))
    };

    const renderDevi = devi =>
        <Ons.ListItem
            modifier="chevron" tappable
            key={devi.id}
            onClick={event => {
                event.stopPropagation();
                dispatch(editDevi(devi));
                dispatch(loadMenuForDevi({id: devi.menuId}))
            }}
        >
            {devi.name}
            <div className="right">
                <Ons.Button
                    disabled={loading}
                    onClick={runCreateFacture(devi)}
                >
                    <Ons.Icon icon="md-receipt"/>
                </Ons.Button> &nbsp;
                <a href={`https://docs.google.com/spreadsheets/d/${devi.spreadsheet}`} target={'_blank'}>
                    <Ons.Button
                        onClick={e => e.stopPropagation()}
                        disabled={loading}
                    >
                        <Ons.Icon icon="fa-print"/>
                    </Ons.Button>
                </a>
                &nbsp;
                <Ons.Button
                    disabled={loading}
                    onClick={(e) => {
                        e.stopPropagation();
                        dispatch(deleteDevis(devi));
                    }}
                >
                    <Ons.Icon icon="fa-trash-alt"/>
                </Ons.Button>
            </div>
        </Ons.ListItem>;


    return editDevis ?
        <Devi
            devi={state.devi}
            status={state.status}
            state={state}

        /> :
        <Ons.Page title="Devis"
                     active={props.activeIndex === 1}
                     renderToolbar={renderToolbar('Devis')}
                     tabbar={props.tabbar}>
            <Ons.ProgressBar indeterminate={loading} />

            <Error error={state.error} resetErr={()=> dispatch(errorReset())}/>

            <Ons.List
            dataSource={devis}
            renderRow={renderDevi}
        />
    </Ons.Page>
};




const devisTab = (activeIndex, tabbar) => {
    return {
        content: <Devis activeIndex={activeIndex}
                        tabbar={tabbar}/>,
        tab: <Ons.Tab label="Devis" icon="fa-file-invoice-dollar"/>
    }
};

export default devisTab;