import {createAsyncThunk, createSlice} from '@reduxjs/toolkit';
import devisApi from "../../api/devisApi";
import menusApi from "../../api/MenuApi";
import factureApi from "../../api/factureApi";
import {activateFactures} from "../../appSlice";
import {refresh} from "../factures/facturesSlice";

export const DevisStatus = {
    loading: 'loading',
    loaded: 'loaded'
};

const initialDevi = {
    id: '',
    to: '',
    address1: '',
    address2: '',
    date: '',
    zip: '',
    city: '',
    unitPrice: '',
    quantity: '',
    price: '',
    menu: {},
    name: '',
    menuId:'',
    spreadsheet:''
};

const initialState = {
    status:'',
    editDevis: false,
    devis: [],
    error: '',
    reload: true,
    devi: initialDevi
};

export const fetchAllDevis = createAsyncThunk(
    'devisfetchAll',
    devisApi.fetchAll
);

export const loadMenuForDevi = createAsyncThunk(
    'devis.loadMenu',
    menusApi.findByMenu
);

export const save = createAsyncThunk(
    'devis.saveMenu',
    devisApi.upsert
);


export const deleteDevis = createAsyncThunk(
    'devis.delete',
    devisApi.deleteDevis
);

export const createFacture = createAsyncThunk(
    'facture.create',
    async (devis, thunkApi) => {
        const menu = await menusApi.findByMenu({id: devis.menuId});
        await factureApi.save({
            ...devis,
            menu
        });

        thunkApi.dispatch(
            activateFactures()
        );
        thunkApi.dispatch(
            refresh()
        )

    }

);

const errorCb = (state, payload) => {
    state.status = DevisStatus.loaded;
    state.error = JSON.stringify(payload);
    state.error = state.error === '' ? 'No error message' : state.error;
    console.error('Failed: ', payload);
};

const devisSlice = createSlice({
    name: 'devis',
    initialState,
    reducers: {
        setDevi:(state, {payload:devi}) =>{
            state.devi = devi;
        },
        setDevisPrice:(state,{payload}) =>{
            state.devi.unitPrice = payload;
            state.devi.price = state.devi.quantity * payload;
        },
        setDevisQuantity:(state,{payload}) =>{
            state.devi.quantity = payload;
            state.devi.price = state.devi.unitPrice * payload;
        },
        closeEdit:state => {
            state.editDevis = false;
            state.status = '';
            state.devi = initialDevi;
        },
        editDevi: (state, {payload:devi})=>{
            state.devi = devi;
        },
        errorReset: state =>{
            state.error = '';
        }

    },
    extraReducers(builder){
        builder.addCase(
            fetchAllDevis.fulfilled,
            (state, {payload}) => {
                state.devis = payload.reverse();
                state.status = DevisStatus.loaded;
            }
        ).addCase(
            fetchAllDevis.pending,
            state => {
                state.status = DevisStatus.loading;
            }
        ).addCase(
            fetchAllDevis.rejected,
            errorCb
        ).addCase(
            loadMenuForDevi.pending,
            state => {
                state.status = DevisStatus.loading;
            }
        ).addCase(
            save.fulfilled,
            state => {
                state.editDevis = false;
                state.status = '';
                state.devi = initialDevi;
            }
        ).addCase(
            save.pending,
            state => {
                state.status = DevisStatus.loading;
            }
        ).addCase(
            save.rejected,
            errorCb
        ).addCase(
            loadMenuForDevi.rejected,
            errorCb
        ).addCase(
            loadMenuForDevi.fulfilled,
            (state, {payload}) => {
                state.devi.menu = payload;
                state.editDevis = true;
                state.status = DevisStatus.loaded;
            }
        ).addCase(
            deleteDevis.rejected,
            errorCb
        ).addCase(
            deleteDevis.pending,
            state => {
                state.status = DevisStatus.loading;
            }
        ).addCase(
            deleteDevis.fulfilled,
            state => {
                state.status = ''
            }
        ).addCase(
            createFacture.rejected,
            errorCb
        ).addCase(
            createFacture.pending,
            state => {
                state.status = DevisStatus.loading;
            }
        ).addCase(
            createFacture.fulfilled,
            state => {
                state.status = ''
            }
        )
    }
});

export const {
    setDevi,
    setDevisPrice,
    setDevisQuantity,
    closeEdit,
    editDevi,
    errorReset
} = devisSlice.actions;

export default devisSlice.reducer;