import {useDispatch} from 'react-redux';
import * as Ons from 'react-onsenui';


const Error = ({error, resetErr}) => {
    const dispatch = useDispatch();

    return <Ons.Modal
        className={'left'}
        isOpen={error !== ''}
    >
        <div className={'left'}>
            <h3>Error</h3>
        </div>
        <p>{error}</p>
        <div className={'left'}>
            <Ons.Button
                onClick={e => {
                    e.stopPropagation();
                    dispatch(resetErr());
                }}
            >OK

            </Ons.Button>
        </div>
    </Ons.Modal>;
};

export default Error
