import * as Ons from 'react-onsenui';
import React from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {newPlat, selectPage} from "../nav/navSlice";
import Plat from "./Plat";
import renderToolbar from "../toolbar/Toolbar";
import {deletePlat, fetchPlats, PlatsStatus, selectPlats, setPlat} from "./platsSlice";
import {DevisStatus} from "../devis/devisSlice";


const Plats = props => {
    const dispatch = useDispatch();

    const page = useSelector(selectPage);

    const plats = useSelector(selectPlats);

    const status = useSelector(state => state.plats.status);

    if(props.active && status === 'refresh') dispatch(fetchPlats());


    const showNewPlat = () => {
        return props.navigator.pushPage({title: 'yes', id: 'newPlat'});
    };

    if (props.route.id === 'newPlat') {
        return <Plat navigator={props.navigator}/>;
    }

    const editPlat = plat => {
        dispatch(setPlat(plat));
        return props.navigator.pushPage({title: 'yes', id: 'newPlat'});
    };


    const loading = status === PlatsStatus.loading;
    return <Ons.Page title="Plats"
                     active={props.active}
                     renderToolbar={renderToolbar('Plats')}
                     tabbar={props.tabbar}
    >
        <Ons.ProgressBar indeterminate={loading} />
        <Ons.List
            dataSource={plats}
            renderRow={(plat, idx) => (
                <Ons.ListItem
                    tappable
                    key={plat.id}
                    modifier={'chevron'}
                    onClick={e => editPlat(plat)}
                >
                    {plat.name}
        <div className="right">
                    <Ons.Button
                        disabled={loading}
                        onClick={e=> {
                            e.stopPropagation();
                            return dispatch(deletePlat(plat));
                        }}
                    ><Ons.Icon icon="fa-trash-alt"/></Ons.Button>
        </div>
                </Ons.ListItem>
            )}/>
        <Ons.Fab position="bottom right" onClick={showNewPlat}>
            <Ons.Icon icon='md-plus'/>
        </Ons.Fab>
    </Ons.Page>;


};

const platsTab = (activeIndex, tabbar, route, navigator) => {
    return {
        content: <Plats active={activeIndex === 3} tabbar={tabbar} navigator={navigator} route={route}/>,
        tab: <Ons.Tab label="Plats" icon="fa-pizza-slice"/>
    }
};

export default platsTab;